import React, { Component } from "react";

class Work extends Component {
    render() {
        const work = this.props.work.map((exp) => {
            return (
                <div className="row">
                    <h3>{exp.name}</h3>
                    <h4>{exp.position}</h4>
                    <h5>{exp.start} <span>&#8212;</span> {exp.end}</h5>
                    <p>{exp.description}</p>
                </div>
            )
        })
        return (
            <>
                This is my work.
                {work}
            </>
        );
    }
}

export default Work;